.App {
  text-align: center;
  height: 100vh;
}

.login-container{
  width: 100%;
  left: 0;
  overflow: hidden;
  touch-action: none;
}

.go{
  height: 100%;
  transform: translateX(-35%);
  transition-property: all;
  transition-duration: 10s;
  transition-delay: 1s;
}

.login-backimg{
  height: 100%;
}

.login-form-container{
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  animation: fadein 15s;
  -moz-animation: fadein 15s;
  /* Firefox */
  -webkit-animation: fadein 15s;
  /* Safari and Chrome */
  -o-animation: fadein 15s;
  /* Opera */
  width: 80%;
  height: 200px;
  z-index: 9999;
  top: 0;
  margin: 10%;
}

.login-form-container > input{
  width: 90%;
  text-align: center;
  height: 25px;
  font-size: 1.25rem;
  border: none;
  border-radius: 10px;
}

.login-form-container > input:focus{
  outline: none;

}

@keyframes fadein{
  from{
    opacity: 0;
  } to {
    opacity: 1;
  }
}

.login-btn{
  margin-top: 2rem;
  font-weight: 700;
}

.main-container{
  width: auto;
  ;
}

.main-topbar{
  width: 100%;
  position: fixed;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 50px;
  background-color: #fff;
}

.mypage-btn{
  width: 30px;
  height: 30px;
  margin-left: 10px;
}

.mypage-btn > img{
  width: 100%;
  height: 100%;
  object-fit: fill;
}

.uploadpage-btn{
  width: 30px;
  height: 30px;
  margin-right: 10px;
}

.uploadpage-btn > img{
  width: 100%;
  height: 100%;
  object-fit: fill;
}

.list-container{
  width: 100%;
  height: 100vh;
  background-color: #f7f7f7;
  display: flex;
  flex-direction: column;
  padding-top: 50px;
  text-align: left;
}

.list-container > h4{
  margin-left: 20px;
}

.list-box{
  width: 100%;
  height: auto;
  display: flex;
  flex-direction: column;
  background-color: #f7f7f7;
}

.list-title{
  width: 100%;
  height: 70px;
  display: flex;
  align-items: center;
  background-color: #f7f7f7;
}

.list-title-userimg{
  width: 40px;
  height: 40px;
  border-radius: 50%;
  overflow: hidden;
  border: 1px solid #e3e3e3;
  margin-left: 10px;
}

.list-title-userimg > img{
  width: 100%;
  height: 100%;
  object-fit: fill;
}

.list-title > h4{
  font-weight: 700;
  margin-left: 10px;
}

.list-title > p{
  font-size: 0.75rem;
  font-weight: 300;
  margin-left: auto;
  margin-right: 10px;
}

.list-img{
  width: 100%;
  height: auto;
}

.list-img > img{
  width: 100%;
  height: 100%;
  object-fit: fill;
}

.list-body{
  width: 90%;
  margin-top: 5%;
  padding: 0 5%;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  background-color: #f7f7f7;
}

.hidden{
  display: none;
}

.list-body-full{
  margin-top: 5%;
  width: 90%;
  padding: 0 5%;
  margin-bottom: 30px;
}

.more-btn{
  margin-top: 10px;
  margin-left: auto;
  margin-right: 5vw;
  margin-bottom: 30px;
}

.upload-container{
  width: 100%;
  height: 100vh;
  touch-action: none;
}

.upload-topbar{
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
}

.back-btn{
  width: 30px;
  height: 30px;
  margin-left: 10px;
}

.back-btn > img{
  width: 100%;
  height: 100%;
  object-fit: fill;
}

.upload-topbar > h2{
  position: relative;
  left: -20px;
  margin: 0 auto;
}

.upload-body{
  display: flex;
  flex-direction: column;
}

.upload-body > h3{
  margin-top: 20px;
  text-align: left;
  margin-left: 10%;
}

.img-upload-btn{
  width: 80%;
  height: 200px;
  border: 2px solid #000;
  border-radius: 20px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}


.name-input{
  width: 70%;
  height: 2rem;
  margin: 0 auto;
  padding: 0 5%;
  border: 2px solid #000;
  border-radius: 20px;
  font-size: 1.125rem;
}


.img-input{
  display: none;
}

.input-img{
  width: 50px;
  height: 50px;
}

textarea{
  width: 70%;
  height: 200px;
  border: 2px solid;
  border-radius: 20px;
  padding: 0 5%;
  font-size: 1.125rem;
  align-self: center;
  resize: none;
}

.upload-body > button{
  width: 80%;
  align-self: center;
  height: 50px;
  margin-top: 20px;
  border: none;
  background-color: #000;
  color: #fff;
  font-size: 1.25rem;
  font-weight: 700;
  border-radius: 25px;
}

.sample-img{
  width: 90%;
  height: 100%;
  object-fit: contain;
}

.userimg-upload-btn{
  width: 60%;
  height: 200px;
  border: 2px solid #000;
  border-radius: 10px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
}